<template>
    <div class="article_item_box">
        <div class="article_item_box_body">
            <div class="article_item_box_body_img">
                <img v-lazy="img">
            </div>
            <div class="article_item_box_body_content">
                <a class="title"><span @click="toDetail">{{articleData.title}}</span></a>
                <div class="content">{{articleData.content}}</div>
                <div class="more">
                    <p class="more-time">{{articleData.time}}</p>
                    <a class="more-detail" @click="toDetail">详情</a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import img1 from '../assets/images/timg2.jpg'
    export default {
        props: {
            articleData:{
                type: Object,
                required: true
            }
        },
        data(){
            return {
                img : img1
            }
        },
        mounted() {

        },
        methods:{
            toDetail(){
                this.$router.push({path:'artDetail'})
            }
        }
    }
</script>
<style lang="scss">
    $prefix: article_item_box;

    .#{$prefix} {
        &_body{
            display: flex;
            border-bottom: 1px solid #c8c4bb;
            border-radius: 2px;
            padding: 10px 0;
            &_img{
                height: 100px;
                margin: 10px 10px 10px 0;
                img{
                    height: 100%;
                    width: 150px;
                }
            }
            &_content{
                margin: 10px;
                .title{
                    font-size: 18px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp:1;
                    -webkit-box-orient: vertical;
                    text-align: justify;
                    line-height: 25px;
                    padding: 2px 0;
                    margin-bottom: 5px;
                    span{
                        cursor: pointer;
                    }
                }
                .title span:hover{
                    text-decoration: underline;
                }
                .more{
                    margin: 5px 0;
                    font-size: 12px;
                    display: flex;
                    justify-content: space-between;
                    .more-time{
                        color: #cac7c0;
                    }
                    .more-detail{
                        color: cadetblue;
                        cursor: pointer;
                    }
                    .more-detail:hover{
                        text-decoration: underline;
                    }
                }
                .content{
                    font-size: 12px;
                    color: #817F77;
                    text-indent: 2em;
                    letter-spacing: 2px;
                    line-height: 16px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp:3;
                    -webkit-box-orient: vertical;
                    text-align: justify;
                    word-wrap: break-word;
                }
            }
            &:hover{
                background-color: rgba(121, 186, 184, 0.1);
            }
        }
    }
</style>
